.shelf-box {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.shelf-card {
    width: 208px !important;
    height: 348px;
    border: 1px solid #f2f2f2;
    border-radius: 4px;
    padding: 16px;
}

.shelf-image {
    margin-bottom: 53px;
}

.shelf-name {
    font-size: 16px;
    margin-bottom: 16px;
}

.shelf-description {
    font-size: 12px;
    color: #ee0e7d;
    margin-bottom: 16px;
}

.shelf-list {
    display: flex;
    flex-direction: column;
    gap: 2px;
    padding: 0 7%;
    margin-bottom: 16px;
}

.shelf-feature {
    font-size: 12px;
    color: #5b5b5b;
    line-height: 18px;
}

.shelf-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 48px;
    background-color: #03ffa5;
    border-radius: 6px;
    font-size: 16px;
}

.shelf-button:hover {
    background-color: #01dc90;
    transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 768px) {
    .shelf-container {
        width: 100%;
        overflow: overlay;
        margin: 56px 0;
    }
    
    .shelf-box {
        width: 200%;
    }
}