@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@500;700&display=swap);
* {
  font-family: 'Barlow', sans-serif;
  margin: 0;
  text-decoration: none;
  color: #000;
}
.header {
    position: fixed;
    top: 0;
    z-index: 4;
    background-color: #fff;
    width: 100%;
}

.main-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 24px;
}

.header-left, .header-right {
    display: flex;
    align-items: center;
    grid-gap: 32px;
    gap: 32px;
}

.header-menu {
    font-size: 16px;
    text-decoration: none;
}

.header-search-container {
    display: flex;
    align-items: center;
    position: relative;
}

.header-search-bar {
    width: 240px;
    height: 40px;
    border-radius: 24px;
    border: 1px solid #f2f2f2;
    color: #ee0e7d;
    font-size: 16px;
    padding: 0 16px;
    position: relative;
    display: flex;
    align-items: center;
}

.header-search-bar::-webkit-input-placeholder {
    color: #ee0e7d;    
}

.header-search-bar::placeholder {
    color: #ee0e7d;    
}

.header-search-submit {
    border: none;
    font-size: 0;
    width: 24px;
    height: 24px;
    background-image: url(/loja-vtex-jussi/static/media/search-icon.01a1d012.svg);
    background-repeat: no-repeat;
    background-color: transparent;
    position: absolute;
    right: 8px;
    padding: 0;
    cursor: pointer;
}

.header-minicart > img {
    width: 24px;
}

@media screen and (max-width: 768px) {
    .main-header {
        display: block;
    }

    .header-left {
        justify-content: space-between;
        grid-gap: 0;
        gap: 0;
    }

    .header-search-container {
        margin-top: 20px;
    }
}
.results-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 40px;
    gap: 40px;
    border-top: 1px solid #b2b2b2;
    box-shadow: 0px 20px 20px 0px #00000040;
    background-color: #fff;
    position: fixed;
    top: 71px;
    z-index: 4;
}

.results-card {
    width: 300px;
    height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.results-image {
    width: 148px;
    height: 180px;
}

.results-title {
    height: 50px;
    text-align: center;
}

@media screen and (max-width: 768px) {
    .results-container {
        top: 131px;
    }
}
.banner-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 560px;
    background-color: #03ffa5;
    margin-top: 71px;
}

.banner-left {
    width: 451px;
}

.banner-title {
    font-size: 64px;
    text-transform: uppercase;
    line-height: 78px;
    margin-bottom: 16px;
}

.banner-description {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 32px;
}

.banner-left-btn {
    padding: 12px 24px;
    border-radius: 6px;
    border: 1px solid #000;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 196px;
}

.banner-left-btn:hover {
    background-color: #000;
    color: #03ffa5;
    transition: all 0.3s ease-in-out;
}

.banner-right {
    width: 460px;
    height: 100%;
    position: relative;
}

.banner-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    width: 209px;
    height: 271px;
    border: 1px solid #f2f2f2;
    border-radius: 8px;
    box-shadow: 0px 32px 32px 0px #00000040;
}

.banner-card:nth-child(1) {
    position: absolute;
    top: 40px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 3;
    grid-gap: 34px;
    gap: 34px;
}
.banner-card:nth-child(2) {
    position: absolute;
    top: 196px;
    left: 0;
    z-index: 2;
    grid-gap: 4px;
    gap: 4px;
}
.banner-card:nth-child(3) {
    position: absolute;
    top: 249px;
    right: 0;
    z-index: 1;
    grid-gap: 24px;
    gap: 24px;
}

.banner-product-btn {
    padding: 12px 24px;
    border: 1px solid #ee0e7d;
    border-radius: 24px;
    color: #ee0e7d;
    display: flex;
    justify-content: center;
    align-items: center;
}

.banner-product-btn:hover {
    background-color: #ee0e7d;
    color: #fff;
    transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 768px) {
    .banner-container {
        padding: 0 20px;
    }

    .banner-title {
        font-size: 45px;
        line-height: 45px;
    }
}
.tipbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 48px;
    gap: 48px;
    background-color: #f2f2f2;
    padding: 28px 0;
}

.tipbar-text {
    font-size: 16px;
}

@media screen and (max-width: 768px) {
    .tipbar-container {
        width: 100%;
        overflow: overlay;
        justify-content: flex-start;
    }
}
.shelf-container {
    margin: 56px 117px;
    display: flex;
    flex-direction: column;
    grid-gap: 24px;
    gap: 24px;
}

.shelf-title-container {
    display: flex;
    align-items: center;
}
.shelf-box {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
}

.shelf-card {
    width: 208px !important;
    height: 348px;
    border: 1px solid #f2f2f2;
    border-radius: 4px;
    padding: 16px;
}

.shelf-image {
    margin-bottom: 53px;
}

.shelf-name {
    font-size: 16px;
    margin-bottom: 16px;
}

.shelf-description {
    font-size: 12px;
    color: #ee0e7d;
    margin-bottom: 16px;
}

.shelf-list {
    display: flex;
    flex-direction: column;
    grid-gap: 2px;
    gap: 2px;
    padding: 0 7%;
    margin-bottom: 16px;
}

.shelf-feature {
    font-size: 12px;
    color: #5b5b5b;
    line-height: 18px;
}

.shelf-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 48px;
    background-color: #03ffa5;
    border-radius: 6px;
    font-size: 16px;
}

.shelf-button:hover {
    background-color: #01dc90;
    transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 768px) {
    .shelf-container {
        width: 100%;
        overflow: overlay;
        margin: 56px 0;
    }
    
    .shelf-box {
        width: 200%;
    }
}
.infocard-container {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 107px;
    gap: 107px;
    width: 100%;
    background: linear-gradient(to right,#fff 0%,#fff 50%,#f2f2f2 50%,#f2f2f2 100%);
    padding: 64px 0;
}

.infocard-text-area {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    grid-gap: 24px;
    gap: 24px;
    width: 240px;
}

.info-card-title {
    font-size: 32px;
    font-weight: 700;
    text-transform: uppercase;
}

.info-card-description {
    font-size: 16px;
}

.info-card-button {
    padding: 12px 24px;
    border: 1px solid #000;
    border-radius: 6px;
    font-size: 16px;
}

.info-card-button:hover {
    background-color: #000;
    color: #fff;
    transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 768px) {
    .infocard-container {
        flex-direction: column-reverse;
        background: linear-gradient(to top,#fff 0%,#fff 50%,#f2f2f2 50%,#f2f2f2 100%);
    }

    .infocard-image-area img {
        width: 100%;
    }
}
.contact-container {
    background-color: #EE0E7D;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 136px 290px;
}

.contact-text {
    font-size: 32px;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
    margin-bottom: 32px;
}

.contact-small {
    font-size: 16px;
    color: #fff;
    margin-bottom: 8px;
}

.contact-link {
    font-size: 32px;
    color: #fff;
}

@media screen and (max-width: 768px) {
    .contact-container {
        padding: 50px;
    }

    .contact-text, .contact-link {
        font-size: 24px;
    }
}
.newsletter-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-gap: 24px;
    gap: 24px;
    background-color: #000;
    padding: 136px 360px;
}

.newsletter-title {
    color: #fff;
    font-size: 32px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
}

.newsletter-form {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #03ffa5;
}

.newsletter-email {
    width: 320px;
    height: 60px;
    background-color: #000;
    color: #fff;
    font-size: 16px;
    border: none;
    outline: none;
}

.newsletter-email::-webkit-input-placeholder {
    font-size: 16px;
    color: #fff;
}

.newsletter-email::placeholder {
    font-size: 16px;
    color: #fff;
}

.newsletter-send {
    height: 40px;
    border: none;
    font-size: 16px;
    color: #03ffa5;
    background-color: #000;
    padding: 0;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .newsletter-container {
        padding: 50px;
    }

    .newsletter-title {
        font-size: 24px;
    }

    .newsletter-email {
        width: 100%;
    }
}
.footer-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #000;
    padding: 27px 0;
}

.social-media-container {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 17px;
    gap: 17px;
}
